.card {
  box-sizing: border-box;
  width: 200px;
  display: flex;
  flex-direction: column;
  background: #fff;
  border: 1px solid #f5f5f5;
  border-radius: 5px;
  padding: 0 16px 16px;

  &__image {
    width: 160px;
    height: 134px;
    margin: 32px 0 40px;
    align-self: center;
  }

  &__product-name {
    line-height: 18px;
    font-weight: 500;
    font-size: 12px;
    color: $primary-color;
    margin-bottom: 4px;
  }

  &__product-code {
    line-height: 14px;
    font-size: 10px;
    color: #616070;
    margin-bottom: 16px;
  }

  &__review {
    display: flex;
    justify-content: space-between;
    margin-bottom: 24px;
  }

  &__review-text {
    align-self: center;
    font-size: 10px;
    line-height: 14px;
    color: $primary-color;
  }

  &__price {
    line-height: 18px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 16px;
  }
}

.price-name {
  font-size: 12px;
  color: #616070;
}

.price-sum {
  font-weight: 700;
  font-size: 16px;
  color: $primary-color;
}

.button {
  box-sizing: border-box;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: $color-stylization;
  border: 1px transparent;
  border-radius: 5px;
  text-decoration: none;
  font-weight: 700;
  font-size: 14px;
  line-height: 16px;
  text-transform: uppercase;
  color: #fff;

  &:hover {
    background-color: #fff;
    border: 1px solid $color-stylization;
    color: $color-stylization;
  }
}
