.catalog {
  display: flex;
  justify-content: center;
  padding: 50px 0;

  &__conteiner {
    display: flex;
    flex-wrap: wrap;
    flex-shrink: 0;
    width: 944px;
  }

  &__card:not(:nth-child(4n)) {
    margin-right: 48px;
  }

  &__card:not(:nth-last-child(-n+4)) {
    margin-bottom: 46px;
  }
}
